var fileManager = NSFileManager.defaultManager()
const pluginName = 'Social Medias & Banner Ads Artboards'
var app = NSApplication.sharedApplication()

const createNew = (context) => {
  const pathArray = context.scriptPath.split('/')
  let defaultsFilePath = pathArray.reverse().filter((e,r) => r !== 0 && r !== 1)
  defaultsFilePath = defaultsFilePath.reverse().join('/')
  defaultsFilePath = defaultsFilePath + '/Resources/defaults.plist'
  const targetDirectory = "/" + context.scriptPath.split('/')[1] + "/" + context.scriptPath.split('/')[2] +"/Library/Application Support/com.bohemiancoding.sketch3/artboards.plist"
  var data = NSData.dataWithContentsOfFile(defaultsFilePath);
  data.writeToFile(targetDirectory)
  context.document.showMessage("ðŸŽ‰ "+pluginName+" have been added to Sketch. Take a look at your 'Custom' artboard presets (A).")
}

export default function(context) {
  const uri = "/" + context.scriptPath.split('/')[1] + "/" + context.scriptPath.split('/')[2] +"/Library/Application Support/com.bohemiancoding.sketch3/artboards.plist"
  const urout = "/" + context.scriptPath.split('/')[1] + "/" + context.scriptPath.split('/')[2] +"/Library/Application Support/com.bohemiancoding.sketch3/artboards.plist"
  const defaultLibraryPath = '~/Library/Application Support/com.bohemiancoding.sketch3/'
  const path = NSHomeDirectory();
  const defaultFilePath = path + '/Library/Application Support/com.bohemiancoding.sketch3/artboards.plist'
  if (fileManager.fileExistsAtPath(String(defaultFilePath)) === 1) {
    const libraryPath  = NSURL.fileURLWithPath(defaultFilePath)
    const array = NSMutableArray.alloc().initWithContentsOfFile(String(defaultFilePath));
    const length = Array.from(array).length
    if (length === 1) {
      const pathArray = context.scriptPath.split('/')
      let defaultsFilePath = pathArray.reverse().filter((e,r) => r !== 0 && r !== 1)
      defaultsFilePath = defaultsFilePath.reverse().join('/')
      defaultsFilePath = defaultsFilePath + '/Resources/defaults.plist'
      const defLibraryPath  = NSURL.fileURLWithPath(defaultsFilePath)
      const karray = NSMutableArray.alloc().initWithContentsOfFile(String(defLibraryPath));
      const xarray = NSMutableArray.alloc().initWithContentsOfFile(String(defaultsFilePath));
      xarray[0] = array[0]
      let x = NSPropertyListSerialization.dataWithPropertyList_format_options_error(xarray, NSPropertyListXMLFormat_v1_0,[],nil)
      NSFileManager.defaultManager().createFileAtPath_contents_attributes(String(urout), x, nil)
      context.document.showMessage("ðŸ’¬ " + pluginName + " have been added to Sketch. Take a look at your custom artboards. (A)")
    } else {
      context.document.showMessage("ðŸ’¬ Looks like " + pluginName + " exists already... Take a look at your custom artboards. (A)")
    }
  } else {
    createNew(context)
  }
}
